}
select {
  border: 1px solid #bbb;
  -webkit-border-radius: 0;
  border-radius: 0;
  height: 3em;
  max-width: 100%;
}
input[type="radio"],
input[type="checkbox"] {
  margin-right: .5em;
}
input[type="radio"] + label,
input[type="checkbox"] + label {
