/*Contact Form*/
.contact-submit,
.form-submit {
  background-color: #009D4e;
  overflow: hidden;
  position: relative;
  display: inline-block;
}
.contact-form textarea,
.contact-form input[type="text"],
.contact-form input[type="email"] {
  width: 100% !important;
  max-width: 100% !important;
}
.contact-submit input[type="submit"],
.form-submit input[type="submit"] {
  background: none;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  font-weight: normal;
  color: #333 !important;
}
.contact-submit input[type="submit"]:hover,
.form-submit input[type="submit"]:hover {
  background: none;
}
.contact-submit:after,
.form-submit:after {
  display: block;
  content: '';
  -ms-transform: rotate(45deg);
  /* IE 9 */
  -webkit-transform: rotate(45deg);
  /* Chrome, Safari, Opera */
  transform: rotate(45deg);
  top: 100%;
  width: 1000px;
  height: 1000px;
  position: absolute;
  left: 20%;
  background: #fff;
  opacity: .2;
  -webkit-transition: all 0.21s ease-in-out;
  -moz-transition: all 0.21s ease-in-out;
  -o-transition: all 0.21s ease-in-out;
  transition: all 0.21s ease-in-out;
}
.contact-submit:hover:after,
.form-submit:hover:after {
  top: -850%;
}
a.btn {
  display: inline-block;
  padding: 7px 25px;
  text-decoration: none;
  color: #fff;
  background: #f48109;
  text-transform: uppercase;
  transition: all .3s ease 0;
  font-size: 1rem;
  position: relative;
  overflow: hidden;
}
a.btn:after {
  display: block;
  content: '';
  -ms-transform: rotate(45deg);
  /* IE 9 */
  -webkit-transform: rotate(45deg);
  /* Chrome, Safari, Opera */
  transform: rotate(45deg);
  top: 100%;
  width: 1000px;
  height: 1000px;
  position: absolute;
  left: 20%;
  background: #fff;
  opacity: .2;
  -webkit-transition: all 0.21s ease-in-out;
  -moz-transition: all 0.21s ease-in-out;
  -o-transition: all 0.21s ease-in-out;
  transition: all 0.21s ease-in-out;
}
a.btn:hover,
a.btn:active {
  text-decoration: none !important;
  text-shadow: none;
  color: #fff;
}
a.btn:hover:after,
a.btn:active:after {
  top: -850%;
}
a.btn-yellow {
  background: #F2D91A;
}
a.btn-red {
  background: #ED3700;
}
a.btn-red:hover,
a.btn-red:focus {
  color: #fff;
}
