}
/* Menu toggle */
.menu-toggle {
  border: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  color: #fff;
  display: none;
  font-size: 14px;
  font-size: .875rem;
  line-height: 1.5;
  margin: 0 auto;
  padding: 1em;
  text-shadow: none;
  background: #009D4e;
}
/* Display the menu toggle when JavaScript is available. */
.js .menu-toggle,
.main-navigation.toggled-on ul.nav-menu {
  display: block;
}
.menu-toggle:hover {
  background: #009D4e;
  -webkit-box-shadow: none;
  box-shadow: none;
  color: #464646;
}
.menu-toggle:focus {
  background: #009D4e;
  -webkit-box-shadow: none;
  box-shadow: none;
  color: #464646;
}
.menu-toggle .icon {
  margin-right: .5em;
  top: -2px;
  color: #fff;
  fill: #fff;
}
.toggled-on .menu-toggle .icon-bars,
.menu-toggle .icon-close {
  display: none;
}
.toggled-on .menu-toggle .icon-close {
  display: inline-block;
}
/* Dropdown Toggle */
.dropdown-toggle {
  background-color: transparent;
  border: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  color: #222;
  display: block;
  font-size: 16px;
  right: -0.5em;
  line-height: 1.5;
  margin: 0 auto;
  padding: .5em;
  position: absolute;
  text-shadow: none;
  top: 0;
}
.dropdown-toggle:hover {
  background: transparent;
}
.dropdown-toggle:focus {
  background: transparent;
  outline: thin dotted;
}
.dropdown-toggle.toggled-on .icon {
  -ms-transform: rotate(-180deg);
  /* IE 9 */
  -webkit-transform: rotate(-180deg);
  /* Chrome, Safari, Opera */
  transform: rotate(-180deg);
}
/* Scroll down arrow */
